import React, { useEffect, useState } from "react";
import { Link, useLocation } from "react-router-dom";
import LinearProgress from "@material-ui/core/LinearProgress";
import axios from "axios";
import { BASE_API_URL } from "../config";
import { toast } from "react-toastify";

import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import Slide from "@material-ui/core/Slide";
import { makeStyles } from "@material-ui/core/styles";


function useQuery() {
  return new URLSearchParams(useLocation().search);
}
const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    "& > * + *": {
      marginTop: theme.spacing(2),
      marginBottom: theme.spacing(2),
    },
  },
}));

 function LinearIndeterminate() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <LinearProgress />
    </div>
  );
}


function VerifyMail() {
  const [isVerified, setVerified] = useState(false);
  const [open, setOpen] = React.useState(true);
  const [isError, setError] = React.useState(false);
  const [message, setMessage] = React.useState('')

  const handleClose = () => {
    setOpen(false);
  };
  const verify = () => {
    setError(false);
    let id = query.get("id");
    if (id === '' || id === null) {
      toast.error('invalid token')
      setError('true')
    }
    axios
      .post(BASE_API_URL + "/users/verifyaccount/" + id)
      .then((response) => {
        toast.success(response.data.success);
        setVerified(true);
      })
      .catch(function (error) {
        setError(true);
        if (error.response) {
          toast.error(error.response.data.error, {
            progress:-0.1
          });
          setMessage(error.response.data.error);
        } else {
          toast.error(error.message)
          
        }
      });
  };

  let query = useQuery();
  useEffect(() => {
    verify();
    //eslint-disable-next-line
  }, []);

  return (
    <div>
      <Dialog
        open={open}
        disableEscapeKeyDown
        disableBackdropClick
        TransitionComponent={Transition}
        keepMounted
        onClose={handleClose}
        aria-labelledby="alert-dialog-slide-title"
        aria-describedby="alert-dialog-slide-description"
      >
        <DialogTitle id="alert-dialog-slide-title">
          {"Account verification notice."}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-slide-description">
            {isVerified && "Account verification successful"}
            {!isVerified && "Please wait while we attempt to verify your account."}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          {!isVerified && !isError && <LinearIndeterminate />}

          {isVerified && !isError && (
            <Button onClick={handleClose} color="primary">
              <Link to="/login">Proceed to Login</Link>
            </Button>
          )}
          {isError && (
            <Button onClick={verify} color="primary">
              Retry
            </Button>
          )}

          {message !== '' && (
            <Button color="primary" onClick={()=> toast.dismiss()}>
              <Link to="/login">Login</Link>
            </Button>
          )}
          {message !== '' && (
            <Button color="primary" onClick={()=> toast.dismiss()}>
              <Link to="/">Go back to home</Link>
            </Button>
          )}
        </DialogActions>
      </Dialog>
    </div>
  );
}

export default VerifyMail;
