import React, { useState, useEffect } from "react";
import axios from "axios";
import { toast } from "react-toastify";
import { BASE_API_URL } from "../config";
export const GeneralContext = React.createContext();

export function GeneralProvider(props) {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [loading, setLoading] = useState(false);
  const [registrationIsComplete, setRegComplete] = useState(false);


  useEffect(() => {
    checkAuth()
   
  }, [isAuthenticated])

  // login
  let login = (values) => {
    setLoading(true);
    axios
      .post(BASE_API_URL + "/users/login", values)
      .then((response) => {
        console.log(response);
        setLoading(false);
        setIsAuthenticated(true);
      })
      .catch(function (error) {
        setLoading(false);
        if (error.response && error.response.data.email) {
          toast.error(error.response.data.email, {progress: -0.1});
        } 
        if (error.response && error.response.data.password) {
          toast.error(error.response.data.password, {progress: -0.1});
        } 
      });
  };
  // register
  let register = (values) => {
    setLoading(true);
    let data = {
      name: values.firstname + " " + values.lastname,
      email: values.email,
      gender: values.gender,
      password: values.password,
      password2: values.confirmpassword,
    };
    console.log(data);
    axios
      .post(BASE_API_URL + "/users/register", data)
      .then((response) => {
        console.log(response);
        setRegComplete(true)
        setLoading(false);
      })
      .catch(function (error) {
        setLoading(false);
        if (error.response && error.response.data.email) {
          toast.error(error.response.data.email, {progress:1})
          }
         else if (error.request) {
          toast.error(error.request);
        } else {
          toast.error(error.message);
        }
      });
  };

  let checkAuth = () => {
    console.log('auth check triggered');
  }



  // context return
  return (
    <GeneralContext.Provider
      value={{ isAuthenticated,registrationIsComplete, loading, setLoading, login, register }}
    >
      {props.children}
    </GeneralContext.Provider>
  );
}
