import React from 'react'
import { Link } from 'react-router-dom'
import ErrorOutlineIcon from "@material-ui/icons/ErrorOutline";
import './notfound.css'
function NotFound() {
  return (
    <div className="container">
      <div className="row mx-0 py-5">
        <div className="col-md-8 d-flex m-auto flex-column justify-content-center">
          <div className="d-flex justify-content-center align-items-center">
            <p className=" err px-1"><ErrorOutlineIcon style={{ fontSize: "4rem", color: "red" }} /></p>
            <p className="display-4 err">Page Not Found!</p>
          </div>
          <div className="msg text-center m-auto h2">
            Maybe this page moved? Got deleted?
            <br /> Is hiding out in quarantine? Never existed in the first
            place?
            <p>
              Let's go <Link to="/">home</Link> and try from there.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}

export default NotFound
