import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import "./components/bootstrap.css";
import "./components/floating-labels.css";
import "./components/bootstrap.bundle.min.js";
import "./fonts/fonts.css";
import App from "./App";
import * as serviceWorker from "./serviceWorker";
import { GeneralProvider } from "./provider/GeneralProvider";
import { BrowserRouter } from "react-router-dom";
import { createMuiTheme, ThemeProvider } from "@material-ui/core";

const theme = createMuiTheme({
  palette: {
    primary: {
      main: "#50b7f5", // This is an orange looking color
    },
    secondary: {
      main: "#ffcc80", //Another orange-ish color
    },
  },
  fontFamily:
    "'Noto Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif !important",
});

ReactDOM.render(
  <GeneralProvider>
    <BrowserRouter>
      <React.StrictMode>
        <ThemeProvider theme={theme}>
          
        <App />
        </ThemeProvider>
      </React.StrictMode>
    </BrowserRouter>
  </GeneralProvider>,
  document.getElementById("root")
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
